    @font-face {
  font-family: "Flaticon";
  src: url("../fonts/dist/flaticons/Flaticon.eot");
  src: url("../fonts/dist/flaticons/Flaticon.eot?#iefix") format("embedded-opentype"),
       url("../fonts/dist/flaticons/Flaticon.woff2") format("woff2"),
       url("../fonts/dist/flaticons/Flaticon.woff") format("woff"),
       url("../fonts/dist/flaticons/Flaticon.ttf") format("truetype"),
       url("../fonts/dist/flaticons/Flaticon.svg#Flaticon") format("svg");
  font-weight: normal;
  font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio:0) {
  @font-face {
    font-family: "Flaticon";
    src: url("../fonts/dist/flaticons/Flaticon.svg#Flaticon") format("svg");
  }
}

